@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Nunito&display=swap);
.menu-area{
    background-color: #7D3CCF;
    width: 300px;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 10px;
}

.menu-titulo{
    height: 125px;

    display: flex;
    align-items: center;
    justify-content: center;
    
    color: white; 
    font-family: 'Nunito';
    font-style: normal;
    font-weight: 800;
    font-size: 36px;
}

.botoes-area{
    cursor: pointer;
}

.menu-botao{
    height: 60px;
    left: 10px;
    top: 140px;

    display: inline-block;
    margin-bottom: 10px;
    background-color: white;
    border-radius: 4px;
    
    -webkit-filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.25));
    
            filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.25));

    font-family: 'Nunito', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px; 
    line-height: 25px;
    display: flex;
    align-items: center;
    text-indent: 20px;
}

.menu-botao:hover{
    opacity: 0.8;
    transition: all ease 0.2s;
}
.lista-area{
    position: fixed;
    top: 0px;
    left: 420px;
    right: 120px;
}

.lista-cabecalho{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    height: 136px;
    margin-bottom: 18px;

}

.lista-botao,
.lista-titulo{
    font-family: 'Nunito', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 49px;
    margin: 18px 0;
}

.lista-botao{
    width: 50px;
    height: 50px;
    background-color: #7D3CCF;
    border-radius: 50%;

    color: white;
    text-align: center;

    -webkit-filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.25));

            filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.25));

    cursor: pointer;
}

.lista-pesquisa{
    border: 2px solid #CCCCCC;
    box-sizing: border-box;
    border-radius: 4px;
    height: 30px;
    
    display: flex;
    align-items: center;
    padding-left: 10px;
    margin-bottom: 18px;

    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 12px;
}

.lista-produto{
    margin-top: 10px;
    height: 50px;
    border-radius: 4px;
    background-color: white;
    -webkit-filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.25));
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;

    font-family: 'Nunito', sans-serif;
    font-style: normal;
    font-weight: bold;
}
